.blog-page {
	.section-title {
		h1 {
			font-size: 35px;
			font-weight: 500;
		}
	}
	.main-section {
		overflow: hidden;
	}
	article {
		border-bottom: 1px solid var(--miscellaneous-color);
		margin-top: 50px;
		padding-bottom: 50px;
	}
	.article-title {
		a {
			color: var(--text-color);
			font-size: 20px;
			line-height: 1.4em;
			font-weight: 500;
			display: block;
		}
	}
	.article-info {
		color: var(--subtext-color);
		font-size: 13px;
		line-height: 1.4em;
	}
}